import React, { Component } from 'react';

import { isLoginOrRedirect, authenticatedAction } from '../../lib/util';
import { postTopicReply } from '../../actions/topic';
import SpinnerCircle from '../shared/spinner-circle';
import styles from './reply-action-bar.css';

export default class ReplyActionBar extends Component {

  constructor(props) {

    super(props);
    this.handleReply = this.handleReply.bind(this);

    this.state = { isLoading: false };
  }

  handleReply() {
    const { dispatch, params: { topicId } } = this.props;

    authenticatedAction(dispatch, () => {
      this.setState({ isLoading: true });
      let replyContent = document.querySelector('#replyContent').innerText;

      //dispatch(postTopicReply(topicId, replyContent))
      //  .then( () => {
      //    replyContent = "";
      //    this.setState({ isLoading: false })
      //  })
      //  .catch( () => {
      //    replyContent = "";
      //    console.log("[ReplyActionBar][handleReply] 回复失败!");
      //    this.setState({ isLoading: false })
      //  })
    });
  }

  renderSpinner() {
    return (
      <div>
        <SpinnerCircle width={20} color={'#9E9E9E'} />
      </div>
    );
  }

  renderReplyText() {
    return (
      <div>回复</div>
    );
  }

  render() {
    return (
      <div className={styles.replyActionBarContainer} onClick={isLoginOrRedirect}>
        <div type="text" size="30" className={styles.replyHint} contentEditable="true" id="replyContent"></div>
        <div className={styles.replyButton} onClick={this.handleReply}>
          { this.state.isLoading ? this.renderSpinner() : this.renderReplyText() }
        </div>
      </div>
    );
  }
}


